@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&family=Roboto&display=swap);
/* ./src/index.css */

:root {
  --background: #edf2f4;
  --secondary-background: #8d99ae;
  --headline: #2b2d42;
  --subheadline: #777777;
  --text: #999999;
  --highlight: #ef233c;
  --button: #d90429;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}

html,
body {
  font-family: -apple-system;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-transform: none;
          transform: none;
  display: block;
  overscroll-behavior-y: none;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  background: #8d99ae;
  background: var(--secondary-background);
  height: 100vh;
}
aside {
  background-color: #edf2f4;
  background-color: var(--background);
}
h1{
  color: #2b2d42;
  color: var(--headline);
  font-family: 'fjalla one', sans;
}
label{
  font-family: 'Roboto', sans-serif;
}
form{
  padding: 40px 50px 20px 50px;
}
input{
  border: none;
	background-color: #2b2d42;
	background-color: var(--headline);
	font-size: 14px;
	letter-spacing: 1.2px;
	transition: 0.3s ease background, color;
	color: white;
	border-radius: 0;
  padding: .8rem;
}
button{
  background-color: #ef233c;
  background-color: var(--highlight);
	color: white;
	text-transform: uppercase;
	letter-spacing: 1.2px;
	padding: 1rem;
	transition: 0.4s ease background;
	width: 100%;
}
input[type="checkbox"]:checked {
  box-shadow: 0 0 0 3px #ef233c;
  box-shadow: 0 0 0 3px var(--highlight);
  background-color: #2b2d42;
  background-color: var(--headline);
}

.name{
  font-family: 'fjalla one', sans;
  color: #2b2d42;
  color: var(--headline)
}
.id{
  font-family: 'Roboto', sans-serif;
  color: #999999;
  color: var(--text)
}
.id:hover{
  color: #edf2f4;
  color: var(--background);
}
.customer{
  z-index: 2;
}
.customer:hover{
  color: #edf2f4;
  color: var(--background)
}
.customer:hover::before{
  content: '';
  display: inline-block;
  background-color: #8d99ae;
  background-color: var(--secondary-background);
  width: 25%;
  height: 5rem;
  /* border-bottom-left-radius: 1rem;
  border-top-left-radius: 1rem; */
  position: fixed;
  border-radius: 100% 0% 87% 13% / 27% 100% 0% 73%  ;
  left: 1rem;
  z-index: -1;
}
/* styling for information blocks */

.block1{
  background-color: #2b2d42;
  background-color: var(--headline);
  color: #999999;
  color: var(--text);
  font-family: 'Roboto', sans-serif;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  height: 40vh;
}

.block1inner{
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.front, .back{
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}
.front{
  background-color: #2b2d42;
  background-color: var(--headline);
  color: #999999;
  color: var(--text);
}
.back{
  background-color: #edf2f4;
  background-color: var(--background);
  color: #2b2d42;
  color: var(--headline);
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.rotate{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.highlight{
  color: #ef233c;
  color: var(--highlight);
}
.highlightbg{
  background-color: #ef233c;
  background-color: var(--highlight);
}
.text{
  color: #999999;
  color: var(--text);
}
.textbg{
  background-color: #999999;
  background-color: var(--text);
}
.textalt{
  color: #2b2d42;
  color: var(--headline)
}
.textaltbg{
  background-color: #999999;
  background-color: var(--text);
  height: 8rem;
  color: #2b2d42;
  color: var(--headline);
  font-family: 'Roboto' sans-serif;
}
.customername{
  position: fixed;
  top: -3rem;
  right: 0;
  color: #2b2d42;
  color: var(--headline);
}
.verticaltext{
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
}
.blockid{
  color: #edf2f4;
  color: var(--background)
}
.iconsbg{
  background-color: #2b2d42;
  background-color: var(--headline);
}
.iconsborder{
  border-color: #ef233c;
  border-color: var(--highlight);
}





.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

